////
/// Components/Drag
/// Sortable
////

.Sortable {
  padding-top: spacing(fat);
  background-color: color(red);

  @media screen and (min-width: breakpoint(tablet)) {
    padding-top: spacing(huger);
  }

  @media screen and (min-width: breakpoint(tablet, wide)) {
    padding-top: spacing(hugest);
  }

  ::selection {
    color: color(red);
  }
}

///
/// Puzzle layout
$puzzle-offset-y-sortable: $cube-height;
$puzzle-width-sortable: $cube-width * 2.5;
$puzzle-height-sortable: $cube-height * 3.25;

.Puzzle--idSortable {
  width: $puzzle-width-sortable;
  height: $puzzle-height-sortable - $puzzle-offset-y-sortable;

  .PuzzleInterior {
    height: $puzzle-height-sortable;
  }
}

///
/// States
.Puzzle--idSortable {
  .draggable-source {
    transition-property: transform;
    transition-duration: duration();
    transition-timing-function: easing(excite);

    // required to reset, otherwise it affects the mouse movement
    &.draggable-mirror {
      transition: none;
    }
  }

  .draggable-source--is-dragging {
    .Face {
      background-color: transparent;

      .Svg {
        opacity: 0;
      }
    }
  }
}

///
/// Cube layout
.Sortable {
  .Cube:nth-child(1) {
    @include cube-layout(3, -5);
  }

  .Cube:nth-child(2) {
    @include cube-layout(2, -6);
  }

  .Cube:nth-child(3) {
    @include cube-layout(1, -7);
  }

  .Cube:nth-child(4) {
    @include cube-layout(0, -8);
  }

  .Cube:nth-child(5) {
    @include cube-layout(3, -3);
  }

  .Cube:nth-child(6) {
    @include cube-layout(3, -1);
  }

  .Cube:nth-child(7) {
    @include cube-layout(3, 1);
  }

  .Cube:nth-child(8) {
    @include cube-layout(2);
  }

  .Cube:nth-child(9) {
    @include cube-layout(1, -1);
  }

  .Cube:nth-child(10) {
    @include cube-layout(0, -6);
  }

  .Cube:nth-child(11) {
    @include cube-layout(0, -4);
  }

  .Cube:nth-child(12) {
    @include cube-layout(0, -2);
  }
}
