////
/// Components
/// Error404
////

// Import `completed` Brand and Stars mixin
@import '../Brand/mixins';
@import '../Stars/mixins';

#Page404 {
  height: 100vh;

  main {
    height: 100%;
  }

  @media screen and (min-width: breakpoint(tablet)) {
    padding: 0;
  }
}

.Error404 {
  @include stars-parent-section;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  background-color: black;

  .Content {
    margin-bottom: 0;
  }

  .Brand:hover {
    @include logo-completed;
  }

  ::selection {
    color: black;
    background-color: color(brand, yellow);
  }
}
