////
/// Utilities
/// Animation
////

///
/// Common keyframe animations
@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

@keyframes hide {
  0% {
    left: 0;
    opacity: 1;
  }

  99% {
    left: 0;
    opacity: 0;
  }

  100% {
    left: -200%;
    opacity: 0;
  }
}

@keyframes svg-trace-stroke {
  to {
    stroke-dashoffset: 0;
  }
}

@keyframes fade-in {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes fade-out {
  to {
    opacity: 0;
  }
}

@keyframes pulse {
  0% {
    opacity: 0;
    transform: scale(0);
  }

  60% {
    opacity: 1;
  }

  90% {
    transform: scale(1.4);
  }

  100% {
    opacity: 0;
  }
}

@keyframes breathe {
  from {
    opacity: 1;
  }

  to {
    opacity: 0.8;
  }
}

@keyframes pop {
    0% { opacity: 0; transform: scale(1); }
   75% { opacity: 1; transform: scale(1.05); }
  100% { transform: scale(1.02); }
}

@keyframes shake {
    0% { transform: translate3d(0, 0, 0); }
   25% { transform: translate3d(0.6rem, 0, 0); }
   50% { transform: translate3d(-1rem, 0, 0); }
   75% { transform: translate3d(0.4rem, 0, 0); }
  100% { transform: translate3d(0, 0, 0); }
}

@keyframes jelly {
    0% { transform: scale(1.000, 1.000); }
    4% { transform: scale(0.947, 0.961); }
    6% { transform: scale(0.967, 0.990); }
    8% { transform: scale(0.999, 1.034); }
   10% { transform: scale(1.033, 1.075); }
   12% { transform: scale(1.041, 1.084); }
   14% { transform: scale(1.068, 1.100); }
   16% { transform: scale(1.071, 1.100); }
   18% { transform: scale(1.081, 1.086); }
   20% { transform: scale(1.082, 1.078); }
   22% { transform: scale(1.079, 1.060); }
   25% { transform: scale(1.073, 1.044); }
   27% { transform: scale(1.070, 1.041); }
   30% { transform: scale(1.059, 1.035); }
   31% { transform: scale(1.057, 1.035); }
   36% { transform: scale(1.047, 1.044); }
   38% { transform: scale(1.046, 1.047); }
   42% { transform: scale(1.044, 1.053); }
   45% { transform: scale(1.045, 1.055); }
   53% { transform: scale(1.049, 1.051); }
   60% { transform: scale(1.051, 1.049); }
   75% { transform: scale(1.050, 1.050); }
  100% { transform: scale(1.050, 1.050); }
}

@keyframes jelly-reverse {
    0% { transform: scale(1.050, 1.050); }
    4% { transform: scale(1.051, 1.049); }
    6% { transform: scale(1.049, 1.051); }
    8% { transform: scale(1.045, 1.055); }
   10% { transform: scale(1.044, 1.053); }
   12% { transform: scale(1.046, 1.047); }
   14% { transform: scale(1.047, 1.044); }
   16% { transform: scale(1.057, 1.035); }
   18% { transform: scale(1.059, 1.035); }
   20% { transform: scale(1.070, 1.041); }
   22% { transform: scale(1.073, 1.044); }
   25% { transform: scale(1.079, 1.060); }
   27% { transform: scale(1.082, 1.078); }
   30% { transform: scale(1.081, 1.086); }
   31% { transform: scale(1.071, 1.100); }
   36% { transform: scale(1.068, 1.100); }
   38% { transform: scale(1.041, 1.084); }
   42% { transform: scale(1.033, 1.075); }
   45% { transform: scale(0.999, 1.034); }
   53% { transform: scale(0.967, 0.990); }
   60% { transform: scale(0.947, 0.961); }
   75% { transform: scale(1.000, 1.000); }
  100% { transform: scale(1.000, 1.000); }
}

///
/// Common animation mixins
@mixin breathe {
  animation: breathe duration(slower) duration(fast) easing() alternate infinite both;
}

@mixin pop-animation {
  animation: pop duration() easing(excite) both;
}

@mixin jelly-animation {
  animation: jelly duration(slower) linear both;
}

@mixin jelly-animation-reverse {
  animation: jelly-reverse duration(slow) linear both;
}
