////
/// Components
/// Stars
////

$star-pattern-size: 60rem;

@keyframes star-drift {
  to {
    transform: translate3d(0, -$star-pattern-size, 0);
  }
}

///
/// Stars
.Stars {
  @include position-cover;
  background: radial-gradient(
    ellipse at bottom,
    color(stars, haze) 0%,
    color(stars) 100%
  );
  overflow: hidden;

  // multiple backgrounds do not seem to be working, so I am using this approach
  &::before {
    @include position-cover;
    content: '';
    display: block;
    background: radial-gradient(
      ellipse at top right,
      color(stars, purple) 0%,
      color(stars, blue) 40%,
      color(stars, coal) 80%,
      color(stars) 100%
    );
  }

  @media screen and (min-width: breakpoint(tablet)) {
    top: -($border-section-width-tablet);
    right: -($border-section-width-tablet);
    left: -($border-section-width-tablet);
  }
}

///
/// Star fields
.StarField {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  height: 175%;
  animation-name: star-drift;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}

.StarField--layerSmall {
  animation-duration: 20s;
}

.StarField--layerMedium {
  animation-duration: 40s;
}

.StarField--layerBig {
  animation-duration: 60s;
}
