////
/// Components/Puzzle
/// Cube
////

.CubesFrame {
  @include position-cover();
}

.Cube {
  // Cubes are positioned within each section's `scss`...
  // If draggable faces: use `top` and `left`
  // If draggable cubes: use a layout function that sets the `translate3d` coordinates
  position: absolute;
  top: 0;
  left: 0;

  .Jelly,
  .JellyInterior {
    width: 100%;
    height: 100%;
  }
}

.Cube--typeSolo {
  width: $cube-width;
  height: $cube-height;
}

