////
/// Components/Features
/// Animation
////

$puzzle-height-animation: $cube-height;

.Puzzle--idAnimation {
  height: $puzzle-height-animation;
}

///
/// Cube layout
.Animation {
  .Cube:nth-child(1) {
    @include cube-layout(0, 1);
  }

  .Cube:nth-child(2) {
    @include cube-layout(4, 1);
  }

  .Cube:nth-child(3) {
    @include cube-layout(8, 1);
  }
}

///
/// Cube juice
$animation-shadow-scale: 0.6;
$animation-cube-distance: spacing(tight);
$animation-cube-distance-tablet: spacing();
$animation-cube-distance-desktop: spacing(looser);

@mixin animation-hover-styles($distance) {
  .Shadow {
    transform: scale($animation-shadow-scale)
               rotateX($isometric-skew)
               translateY($distance * 1.5);
  }

  .Jelly {
    transform: translateY(-$distance);
  }
}

@mixin animation-active-styles {
  .Shadow {
    transition-duration: duration();
    opacity: $puzzle-shadow-opacity;
    transform: scale(0.9) rotateX(0) translateY(0);
  }

  .Jelly {
    transition-duration: duration();
    transform: scale(0.9, 0.85) translateY(1em);
  }
}

.Puzzle--idAnimation {
  .Shadow,
  .Jelly {
    transition-property: opacity, transform;
    transition-duration: duration(slow);
    transition-timing-function: easing(bungie);
  }

  .Cube {
    cursor: path-to-asset(image, 'cursor-rockon.png'), pointer;

    &:hover {
      .Shadow {
        opacity: $puzzle-shadow-opacity / 2.5;
      }

      .JellyInterior {
        @include jelly-animation;
      }

      @include animation-hover-styles($animation-cube-distance);

      @media screen and (min-width: breakpoint(tablet)) {
        @include animation-hover-styles($animation-cube-distance-tablet);
      }

      @media screen and (min-width: breakpoint(tablet, wide)) {
        @include animation-hover-styles($animation-cube-distance-desktop);
      }
    }

    &:active {
      cursor: path-to-asset(image, 'cursor-rockon-clicked.png'), pointer;
      @include animation-active-styles;
    }
  }
}
