////
/// Components/Puzzle
/// Theme
////

///
/// Hero: blue, blue, purple
.Theme-Hero {
  .Face--typeTop {
    background-color: color(purple, light);

    &.draggable-source:hover {
      background-color: color(purple);
    }
  }

  .Face--typeRight {
    background-color: color(brand, blue-light);

    &.draggable-source:hover {
      background-color: color(blue);
    }
  }

  .Face--typeLeft {
    background-color: color(brand, blue-lighter);

    &.draggable-source:hover {
      background-color: color(brand, blue-light);
    }
  }

  &.draggable-source:hover {
    .Face--typeTop {
      background-color: color(purple);
    }

    .Face--typeRight {
      background-color: color(blue);
    }

    .Face--typeLeft {
      background-color: color(brand, blue-light);
    }
  }

  // specific to hero puzzle, previous styles are not used
  .draggable-source:hover .Face--typeTop,
  .draggable-source--is-dragging .Face--typeTop {
    background-color: color(purple);
  }

  .draggable-source:hover .Face--typeRight,
  .draggable-source--is-dragging .Face--typeRight {
    background-color: color(blue);
  }

  .draggable-source:hover .Face--typeLeft,
  .draggable-source--is-dragging .Face--typeLeft {
    background-color: color(brand, blue-light);
  }

  // prevent color change on `:hover` while dragging...
  // unavoidable nasty selectors
  .draggable--is-dragging & .FaceWrapper:not(.draggable-source--is-dragging):hover {
    .Face--typeTop {
      background-color: color(purple, light);
    }

    .Face--typeRight {
      background-color: color(brand, blue-light);
    }

    .Face--typeLeft {
      background-color: color(brand, blue-lighter);
    }
  }
}

