///
/// Basics
.Theme-Basics {
  > .Jelly .Face {
    background-color: color(yellow);
  }
}

///
/// Swappable
.Theme-Swappable {
  .Face--typeRight,
  .Face--typeLeft {
    background-color: color(aqua);
  }
}

.Theme-Swappable--colorYellow {
  .Face--typeTop {
    background-color: color(yellow);

    &:hover {
      // normal shift is too dark, so we use the SASS function instead
      background-color: darken(color(yellow), 8%);
    }
  }
}

.Theme-Swappable--colorRed {
  .Face--typeTop {
    background-color: color(red);

    &:hover {
      background-color: color(red, dark);
    }
  }
}

.Theme-Swappable--colorPurple {
  .Face--typeTop {
    background-color: color(purple);

    &:hover {
      background-color: color(purple, dark);
    }
  }
}

///
/// Collidable
.Theme-Collidable {
  > .Jelly .Face {
    background-color: color(euca);
  }
}
