////
/// Components/Drag
/// Basics
////

.Basics {
  padding-top: spacing(fatter);
  background-color: color(yellow);

  @media screen and (min-width: breakpoint(tablet)) {
    padding-top: spacing(huger);
  }

  @media screen and (min-width: breakpoint(tablet, wide)) {
    padding-top: spacing(jumbo);
  }

  ::selection {
    color: color(yellow);
  }
}

///
/// Puzzle layout
$puzzle-offset-y-basics: $cube-height * 0.5;
$puzzle-height-basics: $cube-height * 3.25;
$puzzle-supplement-width-basics: ($cube-width * 2) + $puzzle-supplement-offset;

.Puzzle--idBasics {
  height: $puzzle-height-basics - $puzzle-offset-y-basics;

  @media screen and (min-width: breakpoint(720p, wide)) {
    width: $puzzle-width-720p;
  }

  .PuzzleInterior {
    height: $puzzle-height-basics;
  }
}

///
/// Shadows
.Shadow--idBasics {
  width: $puzzle-supplement-width-basics;
}

.Shadow--idBasicsLeft {
  left: -$puzzle-supplement-offset-x-multi;
}

.Shadow--idBasicsRight {
  left: auto;
  right: -($puzzle-supplement-offset-x-multi / 2);
}

///
/// States
.Puzzle--idBasics {
  // should be able to remove this once `droppable` is merged
  .draggable-source--is-dragging {
    .Face,
    .Outline--typeCube {
      opacity: 0;
    }
  }
}

///
/// Cube layout
.BasicsCube3,
.BasicsCube7 {
  // force hide cube shadow of any element moved into this DOM order
  // Basics Droppables have no shadow
  .Shadow {
    opacity: 0;
  }
}

// --- Left
.BasicsCube1 {
  @include cube-layout(0, -8);
}

.BasicsCube2 {
  @include cube-layout(2, -8);
}

.BasicsCube3 {
  // droppable cube
  @include cube-layout(1, -5);
}

// --- Right
.BasicsCube4 {
  @include cube-layout(8, -4);

  @media screen and (min-width: breakpoint(720p, wide)) {
    @include cube-layout(10, -4);
  }
}

.BasicsCube5 {
  @include cube-layout(7, -7);

  @media screen and (min-width: breakpoint(720p, wide)) {
    @include cube-layout(9, -7);
  }
}

.BasicsCube6 {
  @include cube-layout(6, -8);

  @media screen and (min-width: breakpoint(720p, wide)) {
    @include cube-layout(8, -8);
  }
}

.BasicsCube7 {
  // droppable cube
  @include cube-layout(7, -5);

  @media screen and (min-width: breakpoint(720p, wide)) {
    @include cube-layout(9, -5);
  }
}

// --- Middle
.BasicsCube8,
.BasicsCube10 {
  @include cube-layout(3, 1);

  @media screen and (min-width: breakpoint(720p, wide)) {
    @include cube-layout(4, 1);
  }
}

.BasicsCube9,
.BasicsCube11 {
  @include cube-layout(5, 1);

  @media screen and (min-width: breakpoint(720p, wide)) {
    @include cube-layout(6, 1);
  }
}
