////
/// Components/Drag
/// Hero
////

@import '../../Stars/mixins';

.Hero {
  @include stars-parent-section;

  padding-top: spacing(loose);
  background-color: color(brand, blue);

  @media screen and (min-width: breakpoint(tablet)) {
    padding-top: spacing(loosest);
  }

  @media screen and (min-width: breakpoint(tablet, wide)) {
    padding-top: spacing(fatter);
  }
}

///
/// Puzzle layout
$puzzle-height-hero: $cube-height * 4;

.Puzzle--idHero {
  height: $puzzle-height-hero - $puzzle-offset-y;

  .PuzzleInterior {
    height: $puzzle-height-hero;
  }
}

.Outline--idHero {
  width: $puzzle-width + ($puzzle-outline-width * 2);
  height: $puzzle-height-hero + ($puzzle-outline-width * 2);
}

///
/// Cube layout
.Hero {
  .Cube:nth-child(1) {
    top: cube-move-y();
    left: cube-move-x(5);
  }

  .Cube:nth-child(2) {
    top: cube-move-y(-1);
    left: cube-move-x(4);
  }

  .Cube:nth-child(3) {
    top: cube-move-y(-1);
    left: cube-move-x(6);
  }

  .Cube:nth-child(4) {
    top: cube-move-y(1);
    left: cube-move-x(2);
  }

  .Cube:nth-child(5) {
    top: cube-move-y(-10);
    left: cube-move-x(3);
  }

  .Cube:nth-child(6) {
    top: cube-move-y(-7);
    left: cube-move-x(2);
  }

  .Cube:nth-child(7) {
    top: cube-move-y(-7);
    left: cube-move-x(4);
  }

  .Cube:nth-child(8) {
    top: cube-move-y(-8);
    left: cube-move-x(3);
  }

  .Cube:nth-child(9) {
    top: cube-move-y(-4);
    left: cube-move-x(3);
  }

  .Cube:nth-child(10) {
    top: cube-move-y(-2);
    left: cube-move-x(3);
  }

  .Cube:nth-child(11) {
    top: cube-move-y();
    left: cube-move-x(3);
  }

  .Cube:nth-child(12) {
    top: cube-move-y(-4);
    left: cube-move-x(5);
  }

  .Cube:nth-child(13) {
    top: cube-move-y(-4);
    left: cube-move-x(7);
  }

  .Cube:nth-child(14) {
    top: cube-move-y(-9);
    left: cube-move-x(6);
  }

  .Cube:nth-child(15) {
    top: cube-move-y(-7);
    left: cube-move-x(6);
  }

  .Cube:nth-child(16) {
    top: cube-move-y(-5);
    left: cube-move-x(6);
  }

  .Cube:nth-child(17) {
    top: cube-move-y(-9);
    left: cube-move-x(8);
  }

  .Cube:nth-child(18) {
    top: cube-move-y(-7);
    left: cube-move-x(8);
  }

  .Cube:nth-child(19) {
    top: cube-move-y(-10);
    left: cube-move-x(5);
  }

  .Cube:nth-child(20) {
    top: cube-move-y(-2);
    left: cube-move-x(1);
  }

  .Cube:nth-child(21) {
    top: cube-move-y(-9);
    left: cube-move-x();
  }

  .Cube:nth-child(22) {
    top: cube-move-y(-7);
    left: cube-move-x();
  }

  .Cube:nth-child(23) {
    top: cube-move-y(-5);
    left: cube-move-x();
  }

  .Cube:nth-child(24) {
    top: cube-move-y(-3);
    left: cube-move-x();
  }

  .Cube:nth-child(25) {
    top: cube-move-y(-10);
    left: cube-move-x(1);
  }

  .Cube:nth-child(26) {
    top: cube-move-y(-11);
    left: cube-move-x(2);
  }

  .Cube:nth-child(27) {
    top: cube-move-y(-5);
    left: cube-move-x(2);
  }
}

.Hero {
  .Outline--typeCube {
    display: none;
  }
}

///
/// Helper numbers
.Hero--isNumbered {
  @for $i from 1 through 27 {
    .Cube:nth-child(#{$i}) {
      &::before {
        content: "#{$i}";
        pointer-events: none;
        z-index: 999;
        position: absolute;
        top: $cube-height / 10;
        left: $cube-width / 6;
        font-size: 2.6rem;
        font-weight: 700;
        text-shadow: 0 0 4px black;
        color: white;
      }
    }
  }
}
