////
/// Components
/// Puzzle
////

.Puzzle {
  @include centered-width; // allow for centering with `max-width`
  position: relative;
  width: $puzzle-width;
  font-size: $puzzle-font-size;

  @media screen and (min-width: breakpoint(tablet)) {
    font-size: $puzzle-font-size-tablet;
  }

  @media screen and (min-width: breakpoint(tablet, wide)) {
    font-size: $puzzle-font-size-desktop;
  }
}

.PuzzleInterior {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

