////
/// Components
/// Svg
////

a.SvgContainer {
  display: block;
  color: black; // default to black
}

.SvgContainer {
  .Svg {
    width: 100%;
    height: 100%;
    overflow: visible;
  }

  .Svg--heightAuto {
    height: auto;
  }
}

.Svg {
  fill: currentColor;
}

///
/// Rotations
.Svg--rotate90 {
  transform: rotate(90deg);
}

.Svg--rotate180 {
  transform: rotate(180deg);
}

.Svg--rotate270 {
  transform: rotate(270deg);
}
