////
/// Components/Drag
/// Swappable
////

.Swappable {
  padding-top: spacing(fat);
  background-color: color(aqua);

  @media screen and (min-width: breakpoint(tablet)) {
    padding-top: spacing(huge);
  }

  @media screen and (min-width: breakpoint(tablet, wide)) {
    padding-top: spacing(hugest);
  }

  ::selection {
    color: color(aqua);
  }
}

///
/// Puzzle layout
$puzzle-width-swappable: $cube-width * 4.5;
$puzzle-height-swappable: $cube-height * 2.75;

.Puzzle--idSwappable {
  width: $puzzle-width-swappable;
  height: $puzzle-height-swappable - $puzzle-offset-y;

  .PuzzleInterior {
    height: $puzzle-height-swappable;
  }
}

.Outline--idSwappable {
  width: $puzzle-width-swappable + ($puzzle-outline-width * 2);
  height: $puzzle-height-swappable + ($puzzle-outline-width * 2);
}

///
/// Cube layout
.Swappable {
  .Cube:nth-child(1) {
    top: cube-move-y(-3);
    left: cube-move-x();
  }

  .Cube:nth-child(2) {
    top: cube-move-y(-4);
    left: cube-move-x(1);
  }

  .Cube:nth-child(3) {
    top: cube-move-y(-5);
    left: cube-move-x(2);
  }

  .Cube:nth-child(4) {
    top: cube-move-y(-6);
    left: cube-move-x(3);
  }

  .Cube:nth-child(5) {
    top: cube-move-y(-1);
    left: cube-move-x(2);
  }

  .Cube:nth-child(6) {
    top: cube-move-y(-2);
    left: cube-move-x(3);
  }

  .Cube:nth-child(7) {
    top: cube-move-y(-3);
    left: cube-move-x(4);
  }

  .Cube:nth-child(8) {
    top: cube-move-y(-4);
    left: cube-move-x(5);
  }

  .Cube:nth-child(9) {
    top: cube-move-y(1);
    left: cube-move-x(4);
  }

  .Cube:nth-child(10) {
    top: cube-move-y();
    left: cube-move-x(5);
  }

  .Cube:nth-child(11) {
    top: cube-move-y(-1);
    left: cube-move-x(6);
  }

  .Cube:nth-child(12) {
    top: cube-move-y(-2);
    left: cube-move-x(7);
  }
}
