////
/// Components/Drag
/// Collidable
////

.Collidable {
  padding-top: spacing(fattest);
  background-color: color(euca);

  @media screen and (min-width: breakpoint(tablet)) {
    padding-top: spacing(huger);
  }

  @media screen and (min-width: breakpoint(tablet, wide)) {
    padding-top: spacing(jumboest);
  }

  ::selection {
    color: color(euca);
  }
}

///
/// Puzzle layout
$puzzle-offset-y-collidable: $cube-height;
$puzzle-height-collidable: $cube-height * 3.5;
$puzzle-shadow-left-width-collidable: ($cube-width * 3) + $puzzle-supplement-offset;
$puzzle-shadow-right-width-collidable: ($cube-width * 2) + $puzzle-supplement-offset;

.Puzzle--idCollidable {
  height: $puzzle-height-collidable - $puzzle-offset-y-collidable;

  @media screen and (min-width: breakpoint(720p, wide)) {
    width: $puzzle-width-720p;
  }

  .PuzzleInterior {
    height: $puzzle-height-collidable;
  }
}

///
/// Shadows
.Shadow--idCollidableLeft {
  bottom: $cube-height / 3;
  left: -$puzzle-supplement-offset-x-multi;
  width: $puzzle-shadow-left-width-collidable;
}

.Shadow--idCollidableRight {
  left: auto;
  right: -($puzzle-supplement-offset-x-multi / 2);
  width: $puzzle-shadow-right-width-collidable;
}

///
/// States
.Puzzle--idCollidable {
  .draggable-source--is-dragging {
    .Face {
      // there could either be a `!important` here...
      // or we can make some adjustments to cube-themes to allow for better :hover styles
      background-color: transparent;

      .Svg {
        opacity: 0;
      }
    }
  }
}

///
/// Cube layout
// --- Left
.CollidableCube1 {
  @include cube-layout(3, 1);
}

.CollidableCube2 {
  @include cube-layout(4);
}

.CollidableCube3 {
  @include cube-layout(2);
}

.CollidableCube4 {
  @include cube-layout(1, -3);
}

.CollidableCube5 {
  @include cube-layout(0, -6);
}

.CollidableCube6 {
  @include cube-layout(1, -7);
}

.CollidableCube7 {
  @include cube-layout(0, -4);
}

// droppable +
// right draggable piece
.CollidableCube8,
.CollidableCube9 {
  @include cube-layout(1, -1);
}

// --- Right
// droppable +
// left draggable piece
.CollidableCube10,
.CollidableCube11 {
  @include cube-layout(8, -3);

  @media screen and (min-width: breakpoint(720p, wide)) {
    @include cube-layout(10, -3);
  }
}

.CollidableCube12 {
  @include cube-layout(6, -3);

  @media screen and (min-width: breakpoint(720p, wide)) {
    @include cube-layout(8, -3);
  }
}

.CollidableCube13 {
  @include cube-layout(7, -8);

  @media screen and (min-width: breakpoint(720p, wide)) {
    @include cube-layout(9, -8);
  }
}

.CollidableCube14 {
  @include cube-layout(6, -9);

  @media screen and (min-width: breakpoint(720p, wide)) {
    @include cube-layout(8, -9);
  }
}

.CollidableCube15 {
  @include cube-layout(7, -6);

  @media screen and (min-width: breakpoint(720p, wide)) {
    @include cube-layout(9, -6);
  }
}

.CollidableCube16 {
  @include cube-layout(7, -4);

  @media screen and (min-width: breakpoint(720p, wide)) {
    @include cube-layout(9, -4);
  }
}

.CollidableCube17 {
  @include cube-layout(4, -6);

  @media screen and (min-width: breakpoint(720p, wide)) {
    @include cube-layout(5, -6);
  }
}
