////
/// Components
/// Button mixins
////

@mixin Button--typeCosmic {
  background-color: transparent;

  &::before {
    opacity: 0;
    transition-duration: duration(completed-puzzle, speed), duration();
    transition-delay: duration(completed-puzzle, delay), 0ms;
  }

  &:focus,
  &:hover {
    &::before {
      transform: none;
    }

    .ButtonInterior {
      background-color: transparent;
    }
  }

  &:active {
    .ButtonInterior {
      background-color: transparent;
    }
  }
}
