////
/// Components/Features
/// Accessible
////

$puzzle-width-accessible: $cube-width * 4;
$puzzle-height-accessible: $cube-height * 1.75;

.Puzzle--idAccessible {
  width: $puzzle-width-accessible;
  height: $puzzle-height-accessible;
}

///
/// Cube layout
.Accessible {
  .Cube:nth-child(3),
  .Cube:nth-child(5) {
    z-index: 1;
  }

  .Cube:nth-child(1),
  .Cube:nth-child(2),
  .Cube:nth-child(4),
  .Cube:nth-child(6) {
    z-index: 2;
  }

  .Cube:nth-child(1) {
    @include cube-layout(0, 1);
  }

  .Cube:nth-child(2) {
    @include cube-layout(1);
  }

  .Cube:nth-child(3) {
    @include cube-layout(2, -1);
  }

  .Cube:nth-child(4) {
    @include cube-layout(3, -2);
  }

  .Cube:nth-child(5) {
    @include cube-layout(4, -1);
  }

  .Cube:nth-child(6) {
    @include cube-layout(5);
  }
}

///
/// Cube juice
@keyframes accessible-wave-left {
  0%,
  50%,
  100% {
    background-color: color(red, light);
  }
  25% {
    background-color: color(yellow, light);
  }
}

@keyframes accessible-wave-right {
  0%,
  50%,
  100% {
    background-color: color(red);
  }
  25% {
    background-color: color(yellow);
  }
}

@keyframes accessible-wave-top {
  0%,
  50%,
  100% { background-color: color(yellow); }
  25% { background-color: color(red); }
}

.Puzzle--idAccessible {
  cursor: path-to-asset(image, 'cursor-rockon.png'), pointer;

  .Face {
    animation-duration: duration(slower);
    animation-timing-function: easing();
    animation-iteration-count: infinite;
    animation-play-state: paused;
  }

  $start: 0ms;
  $delay: duration(fast);

  @for $i from 1 through 6 {
    .Cube:nth-child(#{$i}) {
      .Face {
        animation-delay: $start;
      }
    }

    $start: $start + $delay;
  }

  .Face--typeLeft {
    animation-name: accessible-wave-left;
  }

  .Face--typeRight {
    animation-name: accessible-wave-right;
  }

  .Face--typeTop {
    animation-name: accessible-wave-top;
  }

  &:hover {
    .Face {
      animation-play-state: running;
    }
  }

  &:active {
    cursor: path-to-asset(image, 'cursor-rockon-clicked.png'), pointer;

    .Face {
      animation-duration: duration(slow);
    }
  }
}
