///
/// Fully completed puzzle

// --- Secret key
.Puzzle--idHero {
  .Face--isBlank .Svg {
    opacity: 0;
    fill: none;
    stroke: black;
    // chrome on windows does not seem to give weight to the stroke?
    // perhaps this bit of css will help
    stroke-width: $secret-stroke-width;
  }

  // Vier
  .Cube:nth-child(3) .Face--isBlank .Svg {
    stroke-dasharray: $secret-key-length-vier;
    stroke-dashoffset: $secret-key-length-vier;
  }

  // Agt
  .Cube:nth-child(19) .Face--isBlank .Svg {
    stroke-dasharray: $secret-ley-length-agt;
    stroke-dashoffset: $secret-ley-length-agt;
  }

  // Drie
  .Cube:nth-child(27) .Face--isBlank .Svg {
    stroke-dasharray: $secret-key-length-drie;
    stroke-dashoffset: $secret-key-length-drie;
  }
}

// --- Completed styles
.Hero {
  transition: background-color duration(completed-puzzle, speed) duration(completed-puzzle, delay) easing();
}

.Hero--isCompleted {
  background-color: black;

  .draggable-source {
    cursor: path-to-asset(image, 'cursor-rockon.png'), pointer;

    &:active {
      cursor: path-to-asset(image, 'cursor-rockon-clicked.png'), pointer;
    }
  }

  // requires max specificity
  &.Hero .Cube .Face .Svg {
    transition-duration: duration(completed-puzzle, speed);
    transition-delay: duration(completed-puzzle, delay);
    fill: black;
  }

  .Stars {
    animation: fade-in duration(completed-puzzle, speed) duration(completed-puzzle, delay) easing() both;
  }

  // --- Buttons within Hero
  .Button {
    @include Button--typeCosmic;
  }

  // --- Secret key
  // Vier
  .Cube:nth-child(3) .Face--isBlank .Svg {
    animation: fade-in duration() $secret-fade-delay-vier easing() both,
      svg-trace-stroke duration(slowest) $secret-fade-delay-vier easing() both;
  }

  // Agt
  .Cube:nth-child(19) .Face--isBlank .Svg {
    animation: fade-in duration() $secret-fade-delay-agt easing() both,
      svg-trace-stroke duration(slowest) $secret-fade-delay-agt easing() both;
  }

  // Drie
  .Cube:nth-child(27) .Face--isBlank .Svg {
    animation: fade-in duration() $secret-fade-delay-drie easing() both,
      svg-trace-stroke duration(slowest) $secret-fade-delay-drie easing() both;
  }

  /// --- Prevent :hover styles on theme
  .Theme-Hero {
    .draggable-source:hover .Face--typeLeft {
      background-color: color(brand, blue-lighter);
    }

    .draggable-source:hover .Face--typeRight {
      background-color: color(brand, blue-light);
    }

    .draggable-source:hover .Face--typeTop {
      background-color: color(purple, light);
    }
  }

  // --- Completed Logo Animation
  .Brand:hover {
    @include logo-completed;
  }
}
