////
/// Components/Puzzle
/// State
////

///
/// Page load
.Puzzle--idHero {
  transition: opacity duration(slow) easing();
}

.Section--isLoading {
  .Puzzle--idHero {
    opacity: 0;
  }
}

///
/// Shadow tuck-in
.Shadow--typeCube {
  .draggable-source--is-dragging & {
    opacity: 0;
    transform: translateY(-(spacing()));
  }

  .CollidableCube8 &,
  .CollidableCube10 & {
    display: none;
  }
}

///
/// Common dragging
.draggable--is-dragging {
  &,
  .draggable-source,
  .draggable-mirror {
    cursor: path-to-asset(image, 'cursor-grab.png'), pointer;
  }

  &,
  & > * {
    user-select: none;
  }
}

///
/// Droppable
.draggable-droppable--occupied {
  .Cube {
    transform: none;
  }
}

///
/// Cube Source
.draggable-source {
  cursor: path-to-asset(image, 'cursor-hand.png'), pointer;
}

.draggable-source--placed {
  .JellyInterior {
    @include jelly-animation-reverse;
  }
}

.draggable-source--is-dragging {
  &.FaceWrapper {
    .Face {
      overflow: hidden;

      &::before {
        @include position-cover();
        content: '';
        display: block;
        border-radius: 50%;
        background-color: rgba(white, 0.08);
        box-shadow: 0 0 1.2rem 0.6rem rgba(white, 0.08);
        animation: pulse duration(slower) easing() infinite both;
      }

      .Svg {
        opacity: 0;
      }
    }
  }
}

///
/// Cube Mirror
.draggable-mirror {
  z-index: 999; // make sure it is always on top

  &.Cube {
    transform: none;
  }

  .JellyInterior {
    @include jelly-animation;
  }

  &:not(.Cube--typeSolo) {
    .FacePop {
      @include pop-animation;
    }

    .Face {
      border-radius: radius(tight);
      box-shadow: inset 0 0 0 0.2rem black,
                        0 0 0 0.2rem black;

      @media screen and (min-width: breakpoint(tablet)) {
        box-shadow: inset 0 0 0 0.3rem black,
                          0 0 0 0.3rem black;
      }

      @media screen and (min-width: breakpoint(tablet, wide)) {
        box-shadow: inset 0 0 0 0.5rem black,
                          0 0 0 0.5rem black;
      }
    }
  }

  .Shadow--typeCube {
    // for whatever reason... a transition here just would not work
    display: none;
  }
}

///
/// Collisions
.Cube.isColliding .Jelly {
  animation: shake duration() easing();
}
