////
/// Components
/// Button
////

@import './mixins';

// Import `completed` variables
@import '../Drag/Hero/mixins';

$button-depressed-offset: 0.1rem;

.Button {
  display: inline-block;
  position: relative;
  color: color(brand, yellow);
  background-color: darken(color(brand, blue), 12%);

  &::before {
    @include position-cover();
    content: '';
    display: block;
    background-color: darken(color(brand, blue), 12%);
    transform: translate($spacing-unit, $spacing-unit);
    transition-property: opacity, transform;
    transition-duration: duration();
    transition-timing-function: easing();

    @media screen and (min-width: breakpoint(tablet)) {
      transform: translate(spacing(tightest), spacing(tightest));
    }
  }
}

.ButtonInterior {
  @include flex-item-fix;
  display: inline-flex;
  position: relative;
  z-index: 2;
  flex: 0 0 auto;
  align-items: center;
  justify-content: center;
  padding: spacing(tightest) spacing(tight);
  font-size: type-scale(paragraph);
  font-weight: 700;
  border: border();
  transition-property: color, background-color, border-color;
  transition-duration: duration();
  transition-timing-function: easing();

  @media screen and (min-width: breakpoint(tablet)) {
    font-size: type-scale(paragraph, tablet);
  }
}

.ButtonText,
.ButtonIcon {
  @include flex-item-fix;
  display: block;
  flex: 0 1 auto;
  transition-property: transform;
  transition-duration: duration();
  transition-timing-function: easing();
}

.ButtonText + .ButtonIcon,
.ButtonIcon + .ButtonText {
  margin-left: spacing(tightest);

  @media screen and (min-width: breakpoint(tablet)) {
    margin-left: spacing(tighter);
  }
}

.ButtonText {
  white-space: nowrap;
}

.ButtonIcon {
  width: type-scale(paragraph);

  @media screen and (min-width: breakpoint(tablet)) {
    width: type-scale(paragraph, tablet);
  }
}

///
/// Interaction
.Button {
  &:focus,
  &:hover {
    color: white;

    &::before {
      transform: translate(0, 0);
    }

    .ButtonInterior {
      background-color: darken(color(brand, blue), 20%);
    }
  }

  &:active {
    color: color(ash);

    .ButtonInterior {
      background-color: darken(color(brand, blue), 26%);
    }

    .ButtonText,
    .ButtonIcon {
      transform: translateY($button-depressed-offset);
    }
  }

  &:disabled,
  &.isDisabled {
    pointer-events: none;
    color: color(ink);
  }
}

///
/// Button container
.ButtonContainer {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  margin-top: -(spacing(tighter));
  margin-left: -(spacing(tighter));

  .Button {
    @include flex-item-fix;
    flex: 0 0 auto;
    display: block;
    margin-top: spacing(tighter);
    margin-left: spacing(tighter);
  }

  @media screen and (min-width: breakpoint(tablet)) {
    margin-top: -(spacing(tight));
    margin-left: -(spacing(tight));

    .Button {
      margin-top: spacing(tight);
      margin-left: spacing(tight);
    }
  }
}

///
/// Variants
.Button--typeCosmic {
  @include Button--typeCosmic;
}
