////
/// Components
/// Brand keyframes
////

@keyframes logo-rainbow-mask {
  0% {
    fill: color(brand, blue);
  }
  25% {
    fill: color(brand, purple);
  }
  50% {
    fill: color(brand, red);
  }
  75% {
    fill: color(brand, orange);
  }
}

@keyframes logo-rainbow-purple {
  0% {
    fill: color(brand, purple);
  }
  25% {
    fill: color(brand, red);
  }
  50% {
    fill: color(brand, orange);
  }
  75% {
    fill: color(brand, blue);
  }
}

@keyframes logo-rainbow-red {
  0% {
    fill: color(brand, red);
  }
  25% {
    fill: color(brand, orange);
  }
  50% {
    fill: color(brand, blue);
  }
  75% {
    fill: color(brand, purple);
  }
}

@keyframes logo-rainbow-orange {
  0% {
    fill: color(brand, orange);
  }
  25% {
    fill: color(brand, blue);
  }
  50% {
    fill: color(brand, purple);
  }
  75% {
    fill: color(brand, red);
  }
}

@keyframes logo-outline-bounce {
  25% {
    transform: translate(-0.1rem, 0.1rem);
  }
  75% {
    transform: translate(0.1rem, -0.1rem);
  }
}

///
/// Hero complete animation
@keyframes logo-rainbow-mask-completed {
  0% {
    fill: black;
  }
  25% {
    fill: color(brand, purple);
  }
  50% {
    fill: color(brand, red);
  }
  75% {
    fill: color(brand, orange);
  }
}

@keyframes logo-rainbow-purple-completed {
  0% {
    fill: color(brand, purple);
  }
  25% {
    fill: color(brand, red);
  }
  50% {
    fill: color(brand, orange);
  }
  75% {
    fill: black;
  }
}

@keyframes logo-rainbow-red-completed {
  0% {
    fill: color(brand, red);
  }
  25% {
    fill: color(brand, orange);
  }
  50% {
    fill: black;
  }
  75% {
    fill: color(brand, purple);
  }
}

@keyframes logo-rainbow-orange-completed {
  0% {
    fill: color(brand, orange);
  }
  25% {
    fill: black;
  }
  50% {
    fill: color(brand, purple);
  }
  75% {
    fill: color(brand, red);
  }
}
