////
/// Components/Puzzle
/// Shadow
////

.Shadow {
  pointer-events: none;
  position: absolute;
  left: -($puzzle-supplement-offset / 3);
  bottom: -$puzzle-shadow-offset-y;
  width: 100%;
  margin: auto;
  opacity: $puzzle-shadow-opacity;
}

.Shadow--typeCube {
  bottom: auto;
  top: $puzzle-shadow-offset-y-cube;
  left: -$puzzle-outline-width;
  width: $puzzle-supplement-width-cube;
  // should this be applied to all shadows?
  transition-property: opacity, transform;
  transition-duration: duration();
  transition-timing-function: easing(excite);
}
