////
/// Components
/// Features
////

.Features {
  padding-top: spacing(fattest);
  padding-bottom: spacing();
  background-color: color(purple);

  @media screen and (min-width: breakpoint(tablet)) {
    padding-top: spacing(huger);
    padding-bottom: spacing(fat);
  }

  @media screen and (min-width: breakpoint(tablet, wide)) {
    padding-top: spacing(jumboest);
    padding-bottom: spacing(fatter);
  }

  ::selection {
    color: color(purple);
  }
}
