////
/// Components/Puzzle
/// Face
////

.Face {
  position: absolute;
  width: $face-size;
  height: $face-size;
  transform-origin: 0 0;
  transition: background-color duration() easing();
}

///
/// Default theme
.Face--typeTop {
  @include isometric(top);
}

.Face--typeRight {
  @include isometric(right);
}

.Face--typeLeft {
  @include isometric(left);
}
