////
/// Components/Features
/// Extensible
////

$puzzle-width-extensible: $cube-width * 2.5;
$puzzle-height-extensible: $cube-height * 2.5;

.Puzzle--idExtensible {
  width: $puzzle-width-extensible;
  height: $puzzle-height-extensible;
}

///
/// Cube layout
.Extensible {
  .Cube:nth-child(1) {
    @include cube-layout(0, -1);
  }

  .Cube:nth-child(2) {
    @include cube-layout(0, 1);
  }

  .Cube:nth-child(3) {
    @include cube-layout(1);
  }

  .Cube:nth-child(4) {
    @include cube-layout(2, -1);
  }

  .Cube:nth-child(5) {
    @include cube-layout(3, -2);
  }

  .Cube:nth-child(6) {
    @include cube-layout(2, -3);
  }

  .Cube:nth-child(7) {
    @include cube-layout(1, -4);
  }

  .Cube:nth-child(8) {
    @include cube-layout(0, -5);
  }

  .Cube:nth-child(9) {
    @include cube-layout(0, -3);

    // required for the pattern to look right
    [data-face="top"],
    .CubeOutline--typeTop {
      display: none;
    }
  }
}

///
/// Cube juice

// --- :hover variants
$extensible-face-duration: duration(faster);
$extensible-total-duration: $extensible-face-duration * 18;

@keyframes extensible-wave-left {
  0%,
  50%,
  100% { background-color: color(purple, light); }
  25% { background-color: color(purple, lighter); }
}

@keyframes extensible-wave-top {
  0%,
  50%,
  100% { background-color: color(blue); }
  25% { background-color: color(blue, light); }
}

@keyframes extensible-wave-right {
  0%,
  50%,
  100% { background-color: color(purple); }
  25% { background-color: color(purple, light); }
}

// --- :active variants
$extensible-face-duration-active: duration(faster) - 20ms;
$extensible-total-duration--active: $extensible-face-duration-active * 18;

@keyframes extensible-wave-left--active {
  0%,
  50%,
  100% { background-color: color(purple, light); }
  25% { background-color: color(purple, lightest); }
}

@keyframes extensible-wave-top--active {
  0%,
  50%,
  100% { background-color: color(blue); }
  25% { background-color: color(blue, lighter); }
}

@keyframes extensible-wave-right--active {
  0%,
  50%,
  100% { background-color: color(purple); }
  25% { background-color: color(purple, lighter); }
}

// --- Incremented wave animation --- //
.Puzzle--idExtensible {
  cursor: path-to-asset(image, 'cursor-rockon.png'), pointer;

  .Face {
    animation-duration: $extensible-total-duration;
    animation-timing-function: easing();
    animation-iteration-count: infinite;
    animation-play-state: paused;
  }

  // --- :hover animation

  &:hover {
    .Face {
      animation-play-state: running;
    }
  }

  .Face--typeLeft {
    animation-name: extensible-wave-left;
  }

  .Cube:nth-child(9) {
    .Face--typeLeft {
      animation-delay: $extensible-face-duration;
    }
  }

  .Cube:nth-child(1) {
    .Face--typeLeft {
      animation-delay: $extensible-face-duration * 2;
    }
  }

  .Cube:nth-child(2) {
    .Face--typeLeft {
      animation-delay: $extensible-face-duration * 3;
    }
  }

  .Cube:nth-child(3) {
    .Face--typeLeft {
      animation-delay: $extensible-face-duration * 14;
    }
  }

  .Cube:nth-child(4) {
    .Face--typeLeft {
      animation-delay: $extensible-face-duration * 15;
    }
  }

  .Face--typeTop {
    animation-name: extensible-wave-top;
  }

  .Cube:nth-child(2) {
    .Face--typeTop {
      animation-delay: $extensible-face-duration * 4;
    }
  }

  .Cube:nth-child(3) {
    .Face--typeTop {
      animation-delay: $extensible-face-duration * 5;
    }
  }

  .Cube:nth-child(4) {
    .Face--typeTop {
      animation-delay: $extensible-face-duration * 6;
    }
  }

  .Cube:nth-child(5) {
    .Face--typeTop {
      animation-delay: $extensible-face-duration * 7;
    }
  }

  .Cube:nth-child(6) {
    .Face--typeTop {
      animation-delay: $extensible-face-duration * 16;
    }
  }

  .Cube:nth-child(7) {
    .Face--typeTop {
      animation-delay: $extensible-face-duration * 17;
    }
  }

  .Face--typeRight {
    animation-name: extensible-wave-right;
  }

  .Cube:nth-child(5) {
    .Face--typeRight {
      animation-delay: $extensible-face-duration * 8;
    }
  }

  .Cube:nth-child(6) {
    .Face--typeRight {
      animation-delay: $extensible-face-duration * 9;
    }
  }

  .Cube:nth-child(7) {
    .Face--typeRight {
      animation-delay: $extensible-face-duration * 10;
    }
  }

  .Cube:nth-child(8) {
    .Face--typeRight {
      animation-delay: $extensible-face-duration * 11;
    }
  }

  .Cube:nth-child(9) {
    .Face--typeRight {
      animation-delay: $extensible-face-duration * 12;
    }
  }

  .Cube:nth-child(1) {
    .Face--typeRight {
      animation-delay: $extensible-face-duration * 13;
    }
  }

  // --- :active animation

  &:active {
    cursor: path-to-asset(image, 'cursor-rockon-clicked.png'), pointer;

    .Face {
      animation-duration: $extensible-total-duration--active;
    }

    .Face--typeLeft {
      animation-name: extensible-wave-left--active;
    }

    .Cube:nth-child(9) {
      .Face--typeLeft {
        animation-delay: $extensible-face-duration-active;
      }
    }

    .Cube:nth-child(1) {
      .Face--typeLeft {
        animation-delay: $extensible-face-duration-active * 2;
      }
    }

    .Cube:nth-child(2) {
      .Face--typeLeft {
        animation-delay: $extensible-face-duration-active * 3;
      }
    }

    .Cube:nth-child(3) {
      .Face--typeLeft {
        animation-delay: $extensible-face-duration-active * 14;
      }
    }

    .Cube:nth-child(4) {
      .Face--typeLeft {
        animation-delay: $extensible-face-duration-active * 15;
      }
    }

    .Face--typeTop {
      animation-name: extensible-wave-top--active;
    }

    .Cube:nth-child(2) {
      .Face--typeTop {
        animation-delay: $extensible-face-duration-active * 4;
      }
    }

    .Cube:nth-child(3) {
      .Face--typeTop {
        animation-delay: $extensible-face-duration-active * 5;
      }
    }

    .Cube:nth-child(4) {
      .Face--typeTop {
        animation-delay: $extensible-face-duration-active * 6;
      }
    }

    .Cube:nth-child(5) {
      .Face--typeTop {
        animation-delay: $extensible-face-duration-active * 7;
      }
    }

    .Cube:nth-child(6) {
      .Face--typeTop {
        animation-delay: $extensible-face-duration-active * 16;
      }
    }

    .Cube:nth-child(7) {
      .Face--typeTop {
        animation-delay: $extensible-face-duration-active * 17;
      }
    }

    .Face--typeRight {
      animation-name: extensible-wave-right--active;
    }

    .Cube:nth-child(5) {
      .Face--typeRight {
        animation-delay: $extensible-face-duration-active * 8;
      }
    }

    .Cube:nth-child(6) {
      .Face--typeRight {
        animation-delay: $extensible-face-duration-active * 9;
      }
    }

    .Cube:nth-child(7) {
      .Face--typeRight {
        animation-delay: $extensible-face-duration-active * 10;
      }
    }

    .Cube:nth-child(8) {
      .Face--typeRight {
        animation-delay: $extensible-face-duration-active * 11;
      }
    }

    .Cube:nth-child(9) {
      .Face--typeRight {
        animation-delay: $extensible-face-duration-active * 12;
      }
    }

    .Cube:nth-child(1) {
      .Face--typeRight {
        animation-delay: $extensible-face-duration-active * 13;
      }
    }
  }
}
