////
/// Components/Features
/// Interaction
////

$puzzle-height-interaction: $cube-height * 1.5;
$interaction-shadow-color: #924ecc;

.Puzzle--idInteraction {
  height: $puzzle-height-interaction;
}

///
/// Cube layout
.Interaction {
  // right, inside, bottom
  .Cube:nth-child(6),
  .Cube:nth-child(12) {
    top: cube-move-y(-1);
    left: cube-move-x(6);
  }

  // right, inside, top
  .Cube:nth-child(7),
  .Cube:nth-child(13) {
    top: cube-move-y(1);
    left: cube-move-x(6);
  }

  // middle, bottom
  .Cube:nth-child(1),
  .Cube:nth-child(8),
  .Cube:nth-child(10),
  .Cube:nth-child(18),
  .Cube:nth-child(20),
  .Cube:nth-child(26) {
    top: cube-move-y(-1);
    left: cube-move-x(4);
  }

  // middle, top
  .Cube:nth-child(9),
  .Cube:nth-child(11),
  .Cube:nth-child(19),
  .Cube:nth-child(21),
  .Cube:nth-child(27) {
    top: cube-move-y(1);
    left: cube-move-x(4);
  }

  // right, outside, bottom
  .Cube:nth-child(2),
  .Cube:nth-child(14) {
    top: cube-move-y(-1);
    left: cube-move-x(8);
  }

  // right, outside, top
  .Cube:nth-child(3),
  .Cube:nth-child(15) {
    top: cube-move-y(1);
    left: cube-move-x(8);
  }

  // left, inside, bottom
  .Cube:nth-child(16),
  .Cube:nth-child(22) {
    top: cube-move-y(-1);
    left: cube-move-x(2);
  }

  // left, inside, top
  .Cube:nth-child(17),
  .Cube:nth-child(23) {
    top: cube-move-y(1);
    left: cube-move-x(2);
  }

  // left, outside, bottom
  .Cube:nth-child(4),
  .Cube:nth-child(24) {
    top: cube-move-y(-1);
    left: cube-move-x();
  }

  // left, outside, top
  .Cube:nth-child(5),
  .Cube:nth-child(25) {
    top: cube-move-y(1);
    left: cube-move-x();
  }
}

///
/// Cube juice
.Puzzle--idInteraction {
  cursor: path-to-asset(image, 'cursor-rockon.png'), pointer;

  // --- Shadow
  .Shadow {
    opacity: 1; // ensure full opacity
    color: $interaction-shadow-color;
  }

  .Shadow--idInteractionHover {
    transition-property: transform;
    transition-duration: duration() + duration(faster);
    transition-timing-function: easing(excite);
  }

  .Shadow--idInteractionActive {
    opacity: 0;
    transition-property: opacity, transform;
    transition-duration: duration() + duration(faster);
    transition-timing-function: easing();
  }

  .Cube {
    transition: transform duration() easing(excite);
  }

  // --- Delays
  // right, inside, back
  // left, inside, back
  .Cube:nth-child(6),
  .Cube:nth-child(7),
  .Cube:nth-child(16),
  .Cube:nth-child(17) {
    transition-delay: duration(faster);
  }

  // right, outside, back
  // left, outside, back
  .Cube:nth-child(2),
  .Cube:nth-child(3),
  .Cube:nth-child(4),
  .Cube:nth-child(5) {
    transition-delay: duration(fast);
  }

  // middle, all
  .Cube:nth-child(1),
  .Cube:nth-child(8),
  .Cube:nth-child(9),
  .Cube:nth-child(10),
  .Cube:nth-child(11),
  .Cube:nth-child(18),
  .Cube:nth-child(19),
  .Cube:nth-child(20),
  .Cube:nth-child(21) {
    transition-delay: duration();
  }

  // --- Transforms
  &:hover {
    .Shadow--idInteractionHover {
      transform: translateY($cube-height / 1.9);
    }

    // right, inside, back
    // left, inside, front
    .Cube:nth-child(6),
    .Cube:nth-child(7),
    .Cube:nth-child(22),
    .Cube:nth-child(23) {
      transform: translate3d($cube-width / 2, $cube-height / 4, 0);
    }

    // right, inside, front
    // left, inside, back
    .Cube:nth-child(12),
    .Cube:nth-child(13),
    .Cube:nth-child(16),
    .Cube:nth-child(17) {
      transform: translate3d(-($cube-width / 2), $cube-height / 4, 0);
    }

    // middle, right, back
    // middle, right, front
    .Cube:nth-child(8),
    .Cube:nth-child(9),
    .Cube:nth-child(10),
    .Cube:nth-child(11) {
      transform: translate3d($cube-width, $cube-height / 2, 0);
    }

    // middle, left, back
    // middle, left, front
    .Cube:nth-child(18),
    .Cube:nth-child(19),
    .Cube:nth-child(20),
    .Cube:nth-child(21) {
      transform: translate3d(-$cube-width, $cube-height / 2, 0);
    }
  }

  &:active {
    cursor: path-to-asset(image, 'cursor-rockon-clicked.png'), pointer;

    .Shadow--idInteractionActive {
      opacity: 1;
      transform: translateY($cube-height / 1.25);
    }

    // middle, right, front
    .Cube:nth-child(10),
    .Cube:nth-child(11) {
      transform: translate3d($cube-width * 1.5, $cube-height * 0.75, 0);
    }

    // middle, left, front
    .Cube:nth-child(20),
    .Cube:nth-child(21) {
      transform: translate3d(-($cube-width * 1.5), $cube-height * 0.75, 0);
    }

    // right, outside, back
    .Cube:nth-child(2),
    .Cube:nth-child(3) {
      transform: translate3d(-($cube-width / 2), -($cube-height / 4), 0);
    }

    // left, outside, back
    .Cube:nth-child(4),
    .Cube:nth-child(5) {
      transform: translate3d($cube-width / 2, -($cube-height / 4), 0);
    }

    // middle, back
    .Cube:nth-child(1) {
      transform: translateY($cube-height / 2);
    }
  }
}
