////
/// Components
/// Notification
////

@import './keyframes';

$notification-icon-size: 3.2rem;

.Notification {
  @include centered-width(layout-width());
  display: block;
  position: fixed;
  z-index: 999;
  right: spacing(tighter);
  bottom: spacing(tighter);
  left: spacing(tighter);
  text-align: left;
  border-radius: radius(loose);
  box-shadow: shadow(subtle);
  background-color: color(brand, blue);
  transition: background-color duration() easing();
  animation: notification-enter duration(slow) easing(bungie) both;

  &:focus {
    // reapply style because of reset
    box-shadow: shadow(subtle);
  }

  &:focus,
  &:hover {
    background-color: darken(color(brand, blue), 4%);

    .NotificationIcon {
      transform: translate(0.2rem, -0.2rem);
    }
  }

  &:active {
    background-color: color(brand, blue);

    .NotificationIcon {
      transform: translate(0, 0);
    }
  }
}

.NotificationInterior {
  display: flex;
  align-items: center;
  padding: spacing(tighter);
}

.NotificationIcon {
  flex: 0 0 auto;
  margin-right: spacing(tighter);
  width: $notification-icon-size;
  transition: transform duration() easing();

  .Svg {
    fill: color(brand, yellow);
  }
}

.NotificationMessage {
  flex: 1 1 auto;
  font-size: type-scale(paragraph);
  font-weight: 600;
  line-height: 1.3;
  color: white;
  user-select: none;
  // reset the cursor set for typography
  cursor: path-to-asset(image, 'cursor-pointer.png'), pointer;
}
