////
/// Components
/// Brand
////

@import './keyframes';
// @import './mixins';

$logo-draggable-width: 4.6rem;
$logo-draggable-width-tablet: 6rem;
$wordmark-draggable-width: 23.4rem;
$wordmark-draggable-width-tablet: 32rem;

.Brand {
  cursor: path-to-asset(image, 'cursor-rockon.png'), pointer;
  margin-bottom: spacing(tight);
  margin-left: auto;
  margin-right: auto;
  width: $wordmark-draggable-width;

  @media screen and (min-width: breakpoint(tablet)) {
    width: $wordmark-draggable-width-tablet;
  }

  &:active {
    cursor: path-to-asset(image, 'cursor-rockon-clicked.png'), pointer;
  }
}

.BrandLogo {
  margin-left: auto;
  margin-right: auto;
  width: $logo-draggable-width;

  @media screen and (min-width: breakpoint(tablet)) {
    width: $logo-draggable-width-tablet;
  }
}

.Svg--srcDraggable {
  // necessary to prevent cutoff on :hover animation
  // this SVG is also unique in that it has been separated into a view partial,
  // rather than being apart of the compiled Symbols
  overflow: visible;
}

.BrandWordmark {
  .Svg {
    fill: color(brand, yellow);
  }
}

///
/// Logo
.Wave--colorMask {
  // transparent so it doesn't appear to instantly switch colors
  // when changing to the `completed` state
  fill: transparent;
}

.Wave--colorPurple {
  fill: color(brand, purple);
}

.Wave--colorRed {
  fill: color(brand, red);
}

.Wave--colorOrange {
  fill: color(brand, orange);
}

.Hand {
  fill: color(brand, yellow);
}

// --- Interaction
.Brand:hover {
  .Wave--colorMask {
    animation: logo-rainbow-mask duration(slow) easing() infinite;
  }

  .Wave--colorPurple {
    animation: logo-rainbow-purple duration(slow) easing() infinite;
  }

  .Wave--colorRed {
    animation: logo-rainbow-red duration(slow) easing() infinite;
  }

  .Wave--colorOrange {
    animation: logo-rainbow-orange duration(slow) easing() infinite;
  }

  .Hand {
    animation: logo-outline-bounce duration(slow) linear infinite;
  }
}

.Brand:active {
  .Wave--colorMask,
  .Wave--colorPurple,
  .Wave--colorRed,
  .Wave--colorOrange,
  .Hand {
    animation-duration: duration();
  }
}
