////
/// Components/Puzzle
/// Outline
////

.Outline {
  pointer-events: none;
  position: absolute;
  top: -($puzzle-outline-width + ($puzzle-outline-width / 4));
  left: -$puzzle-outline-width;
  margin: auto;
  width: 100%;
  height: 100%;

  .Svg {
    fill: black;

    @media screen and (max-width: breakpoint(tablet, wide) - 1px) {
      stroke: black;
      stroke-width: $puzzle-outline-stroke;
      stroke-linecap: round;
    }
  }
}

.Outline--typeCube {
  top: -$puzzle-outline-width;
  left: -$puzzle-outline-width;
  width: $puzzle-supplement-width-cube;
  height: $puzzle-supplement-height-cube;
}

.CubeOutline {
  @include position-cover();
}
